import React,{useState} from 'react'
import {useStaticQuery, graphql ,Link} from 'gatsby'
function SidebarMenu() {
  const data = useStaticQuery(graphql`
		query{
			logo: file(relativePath: {eq: "header-logo2.png"}) {
				id
				childImageSharp {
				  fluid {
					originalImg
				  }
				}
			  }
		}`
	)

	const [subMenu,setSubMenu]=useState(false);
	const [subMenu2,setSubMenu2]=useState(false);
    return (
        <div className="dashboard_sidebar_menu dn-992">
	    <ul className="sidebar-menu">
	   		<li className="header">
               <img src={data.logo.childImageSharp.fluid.originalImg} alt="header-logo2.png"/> FindHouse</li>
	   		<li className="title"><span>Main</span></li>
	    	<li className="treeview"><Link to="/user/dashboard"><i className="flaticon-layers"></i><span> Dashboard</span></Link></li>
	      	<li className="treeview"><Link to="/user/message"><i className="flaticon-envelope"></i><span> Message</span></Link></li>
	   		<li className="title"><span>Manage Listings</span></li>
          	<li className={subMenu ? "treeview active" : "treeview"} onClick={()=>{
						setSubMenu(!subMenu)
					}}>
		        <Link to="#"><i className="flaticon-home"></i> <span>My Properties</span><i className="fa fa-angle-down pull-right"></i></Link>
		        <ul className={ subMenu ? "treeview-menu menu-open" : "treeview-menu" }>
		        	<li><Link to="/user/add-new-property"><i className="fa fa-circle"></i> Add New Property</Link></li>
		        	<li><Link to="/user/properties"><i className="fa fa-circle"></i> My Properties</Link></li>
		        	<li><Link to="#"><i className="fa fa-circle"></i> Editors</Link></li>
		        </ul>
	      	</li>
	      	<li><Link to="/user/favorites"><i className="flaticon-heart"></i> <span> My Favorites</span></Link></li>
	      	<li><Link to="/user/search"><i className="flaticon-magnifying-glass"></i> <span>Saved Search</span></Link></li>
	     	<li className={ subMenu2 ? "treeview active" : "treeview"} onClick={()=>{
						setSubMenu2(!subMenu2)
					}}>
		        <Link to="#"><i className="flaticon-chat"></i><span> Reviews</span><i className="fa fa-angle-down pull-right"></i></Link>
		        <ul className={ subMenu2 ? "treeview-menu menu-open" : "treeview-menu" }>
		        	<li><Link to="/user/review"><i className="fa fa-circle"></i> My Reviews</Link></li>
		        	<li><Link to="/user/review"><i className="fa fa-circle"></i> Visitor Reviews</Link></li>
		        </ul>
	      	</li>
	   		<li className="title"><span>Manage Account</span></li>
		    <li><Link to="/user/packages"><i className="flaticon-box"></i> <span>My Package</span></Link></li>
		    <li><Link to="/user/profile"><i className="flaticon-user"></i> <span>My Profile</span></Link></li>
		    <li><Link to="/login"><i className="flaticon-logout"></i> <span>Logout</span></Link></li>
	    </ul>
    </div>
    )
}

export default SidebarMenu
