import React, { useState } from 'react'
import { Link } from 'gatsby'

function SideBarMobile({active}) {
    const [DropdownOpen, setDropdownOpen]=useState(false)
    return (
        <div className="dashboard_navigationbar dn db-992">
        <div className="dropdown">
            <button onClick={()=>{setDropdownOpen(!DropdownOpen)}} className="dropbtn"><i className="fa fa-bars pr10"></i> Dashboard Navigation</button>
           {DropdownOpen ? 
           <ul id="myDropdown" className="dropdown-content show">
                <li className={active==="1" ? 'active' : null}><Link to="/user/dashboard"><span className="flaticon-layers"></span> Dashboard</Link></li>
                <li className={active==="2" ? 'active' : null}><Link to="/user/message"><span className="flaticon-envelope"></span> Message</Link></li>
                <li className={active==="3" ? 'active' : null}><Link to="/user/properties"><span className="flaticon-home"></span> My Properties</Link></li>
                <li className={active==="4" ? 'active' : null}><Link to="/user/favorites"><span className="flaticon-heart"></span> My Favorites</Link></li>
                <li className={active==="5" ? 'active' : null}><Link to="/user/search"><span className="flaticon-magnifying-glass"></span> Saved Search</Link></li>
                <li className={active==="6" ? 'active' : null}><Link to="/user/review"><span className="flaticon-chat"></span> My Reviews</Link></li>
                <li className={active==="7" ? 'active' : null}><Link to="/user/packages"><span className="flaticon-box"></span> My Package</Link></li>
                <li className={active==="8" ? 'active' : null}><Link to="/user/profile"><span className="flaticon-user"></span> My Profile</Link></li>
                <li className={active==="9" ? 'active' : null}><Link to="/user/add-new-property"><span className="flaticon-filter-results-button"></span> Add New Listing</Link></li>
                <li><Link to="/user/login"><span className="flaticon-logout"></span> Logout</Link></li>
            </ul>
            : null
           }
             </div>
        </div>
    )
}

export default SideBarMobile
