import React,{useState} from 'react'
import {useStaticQuery, graphql} from 'gatsby'
import SideBarMobile from '../../../Global/App/User/SideBarMobile.component'
import Pagination from '../../../Global/Pages/Pagination.component'
import {Dropdown} from 'react-bootstrap'

const FavoritesSection=() =>{
     const data = useStaticQuery(graphql`
	query{
		fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
    }`)
    const [selectValue, setSelectValue] = useState("Featured First");
    const toggleSelect = (e) => {
        setSelectValue(e);
    }
    return (
       <section className="our-dashbord dashbord bgc-f7 pb50">
		<div className="container-fluid">
			<div className="row">
				<div className="col-lg-3 col-xl-2 dn-992 pl0"></div>
				<div className="col-sm-12 col-lg-8 col-xl-10 maxw100flex-992">
					<div className="row">
						<div className="col-lg-12">
							<div className="dashboard_navigationbar dn db-992">
								<SideBarMobile active="4"/>
							</div>
						</div>
						<div className="col-lg-4 col-xl-4 mb10">
							<div className="breadcrumb_content style2 mb30-991">
								<h2 className="breadcrumb_title">My Favorites</h2>
								<p>We are glad to see you again!</p>
							</div>
						</div>
						<div className="col-lg-8 col-xl-8">
							<div className="candidate_revew_select style2 text-right mb30-991">
								<ul className="mb0">
									<li className="list-inline-item">
										<div className="candidate_revew_search_box course fn-520">
											<form className="form-inline my-2">
										    	<input className="form-control mr-sm-2" type="search" placeholder="Search Courses" aria-label="Search"/>
										    	<button className="btn my-2 my-sm-0" type="submit"><span className="flaticon-magnifying-glass"></span></button>
										    </form>
										</div>
									</li>
									<li className="list-inline-item">
                                    <Dropdown className="bootstrap-select"  onSelect={(e) => toggleSelect(e)}>
                                        <Dropdown.Toggle className="btn dropdown-toggle">
                                            <span className="filter-option">
												<span className="filter-option-inner">
                                          			  <span className="filter-option-inner-inner">{selectValue}</span>
                                           		 </span>
                                            </span>
                                        </Dropdown.Toggle>
                                        <Dropdown.Menu>
                                            <Dropdown.Item as="option" eventKey="Featured First" >Featured First</Dropdown.Item>
                                            <Dropdown.Item as="option" eventKey="Recent" >Recent</Dropdown.Item>
                                            <Dropdown.Item as="option" eventKey="Old Review" >Old Review</Dropdown.Item>
                                        </Dropdown.Menu>
                                    </Dropdown>
									
									</li>
								</ul>
							</div>
						</div>
						<div className="col-lg-12">
        <div className="my_dashboard_review mb40">
								<div className="favorite_item_list">
									<div className="feat_property list favorite_page">
										<div className="thumb">
											<img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
											<div className="thmb_cntnt">
												<ul className="tag mb0">
													<li className="list-inline-item dn"></li>
													<li className="list-inline-item"><a href="#">For Rent</a></li>
												</ul>
											</div>
										</div>
										<div className="details">
											<div className="tc_content">
												<h4>Renovated Apartment</h4>
												<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
												<a className="fp_price text-thm" href="#">$13,000<small>/mo</small></a>
											</div>
										</div>
						    			<ul className="view_edit_delete_list mb0 mt35">
						    				<li className="list-inline-item" data-toggle="tooltip" data-placement="top" title="Delete"><a href="#"><span className="flaticon-garbage"></span></a></li>
						    			</ul>
									</div>
									<div className="feat_property list favorite_page mt30">
										<div className="thumb">
											<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp2.jpg"/>
											<div className="thmb_cntnt">
												<ul className="tag mb0">
													<li className="list-inline-item dn"></li>
													<li className="list-inline-item"><a href="#">For Rent</a></li>
												</ul>
											</div>
										</div>
										<div className="details">
											<div className="tc_content">
												<h4>Luxury Family Home</h4>
												<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
												<a className="fp_price text-thm" href="#">$13,000<small>/mo</small></a>
											</div>
										</div>
						    			<ul className="view_edit_delete_list mb0 mt35">
						    				<li className="list-inline-item" data-toggle="tooltip" data-placement="top" title="Delete"><a href="#"><span className="flaticon-garbage"></span></a></li>
						    			</ul>
									</div>
									<div className="feat_property list favorite_page mt30">
										<div className="thumb">
											<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
											<div className="thmb_cntnt">
												<ul className="tag mb0">
													<li className="list-inline-item dn"></li>
													<li className="list-inline-item"><a href="#">For Rent</a></li>
												</ul>
											</div>
										</div>
										<div className="details">
											<div className="tc_content">
												<h4>Gorgeous Villa Bay View</h4>
												<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
												<a className="fp_price text-thm" href="#">$13,000<small>/mo</small></a>
											</div>
										</div>
						    			<ul className="view_edit_delete_list mb0 mt35">
						    				<li className="list-inline-item" data-toggle="tooltip" data-placement="top" title="Delete"><a href="#"><span className="flaticon-garbage"></span></a></li>
						    			</ul>
									</div>
									<div className="feat_property list favorite_page mt30">
										<div className="thumb">
											<img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
											<div className="thmb_cntnt">
												<ul className="tag mb0">
													<li className="list-inline-item dn"></li>
													<li className="list-inline-item"><a href="#">For Rent</a></li>
												</ul>
											</div>
										</div>
										<div className="details">
											<div className="tc_content">
												<h4>Ample Penthouse</h4>
												<p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
												<a className="fp_price text-thm" href="#">$13,000<small>/mo</small></a>
											</div>
										</div>
						    			<ul className="view_edit_delete_list mb0 mt35">
						    				<li className="list-inline-item" data-toggle="tooltip" data-placement="top" title="Delete"><a href="#"><span className="flaticon-garbage"></span></a></li>
						    			</ul>
									</div>
									<Pagination/>
								</div>
							</div>
                        </div>
					</div>
					<div className="row mt10">
						<div className="col-lg-12">
							<div className="copyright-widget text-center">
								<p>© 2020 Find House. Made with love.</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
    )
}

export default FavoritesSection
